import React, { Component } from 'react';
import {MainTable} from "./mainTable";

class NewRecordForm extends Component{
    constructor(props){
        super(props);
        this.state = {
            chosenMonth: "",
            chosenYear: "",
            homePoint: "",
            workPoint: "",
            btn: false,
            routeLength: "",
            select: ""
        }
    }

    choseMonth = (event) => {
        this.setState ({
            chosenMonth: event.target.value
        })
    };
    choseYear = (event) => {
        this.setState ({
            chosenYear: event.target.value
        })
    };
    changeA = (event) => {
        this.setState ({
            homePoint: event.target.value
        })
    };
    changeB = (event) => {
        this.setState ({
            workPoint: event.target.value
        })
    };
    changeC = (event) => {
        this.setState ({
            routeLength: event.target.value
        })
    };
    create = () => {
        this.setState ({
            btn: true
        })
    };
    changeSelect = (event) => {
        this.setState ({
            select: event.target.value
        })
    };
    render(){
        return (
            <div className="newRecordForm container">
                <div className="boxTableData" >
                    <div className="calendar">
                        <div><br/>
                            Wybierz miesic <br/>
                            <select onChange={event => this.choseMonth(event)} value={this.state.choseMonth}>
                                <option value={0}></option>
                                <option value={1}>Stycze</option>
                                <option value={2}>Luty</option>
                                <option value={3}>Marzec</option>
                                <option value={4}>Kwiecie</option>
                                <option value={5}>Maj</option>
                                <option value={6}>Czerwiec</option>
                                <option value={7}>Lipiec</option>
                                <option value={8}>Sierpie</option>
                                <option value={9}>Wrzesie</option>
                                <option value={10}>Padziernik</option>
                                <option value={11}>Listopad</option>
                                <option value={12}>Grudzie</option>
                            </select>
                        </div>
                        <div><br/>
                            Wybierz rok<br/>
                            <select onChange={event => this.choseYear(event)} value={this.state.choseYear}>
                                <option></option>
                                <option>2018</option>
                                <option>2017</option>
                                <option>2016</option>
                                <option>2015</option>
                                <option>2014</option>
                                <option>2013</option>
                            </select>
                        </div>

                    </div>

                </div>

                <div className="createKM">
                    <button onClick={this.create}>Stwrz KILOMETRWK</button>
                </div>
                {this.state.btn === true && <MainTable select={this.state.select} capacity={this.props.capacity} km={this.state.routeLength} workPoint={this.state.workPoint} homePoint={this.state.homePoint} chosenMonth={this.state.chosenMonth} chosenYear={this.state.chosenYear}/>}
                </div>
        )
    }
}

export {NewRecordForm}


