import React, { Component } from 'react';

class Instruction extends Component{
    render () {
        return (
            <div className="discription">

                <ol>             <h1>Jak stworzy kilometrwk</h1>
                    <li>Wprowad dane przedsibiorstwa</li>
                    <li>Wprowad dane kierowcy</li>
                    <li>Wprowad numer rejestracyjny pojazdu oraz wybierz pojemno</li>
                    <li>Wprowad tras z domu do pracy</li>
                    <li>Wybierz miesic i rok</li>
                    <li>Nacinij - STWRZ KILOMETRWK</li>
                </ol>
            </div>
        )
    }
}

export {Instruction}



