import React, { Component } from 'react';

class Header extends Component{
    render() {
        return (
            <header>
                <div className="container">
                    <div className="mainLogo"></div>
                    <div className="aplicationDescript">KILOMETRWKA - jest to aplikacja do generowania ewidencji przebiegu pojazdu dla przedsibiorcw, ktrzy uywaj swojego samochodu w celach subowych. Masz moliwoc edycji caej kilometrwki na wiele spsobw.</div>
                </div>

            </header>
        )
    }
}

export {Header}


